
header.masthead {
    position: relative;
    background: #e3cc90 url("./images/slide_wir-ueber-uns_01.jpg") no-repeat center center;
    background-size: cover;
    padding-top: 8rem;
    padding-bottom: 8rem;
}

header.masthead .overlay {
    position: absolute;
    background-color: #e3cc90;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    opacity: 0.3;
}

.header-video {
    min-width: 640px;
    min-height: 360px;
}

header.masthead h1 {
    font-size: 2rem;
}

@media (min-width: 780px) {
    header.masthead {
        padding-top: 12rem;
        padding-bottom: 12rem;
    }
    header.masthead h1 {
        font-size: 3rem;
    }
}